@keyframes layer-bounceOut {
  100% {
    opacity: 0;
    transform: scale(0.7);
  }
  30% {
    transform: scale(1.05);
  }
  0% {
    transform: scale(1);
  }
}
@keyframes layui-rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes layer-bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes layer-zoomInDown {
  0% {
    opacity: 0;
    transform: scale(0.1) translateY(-2000px);
    animation-timing-function: ease-in-out;
  }
  60% {
    opacity: 1;
    transform: scale(0.475) translateY(60px);
    animation-timing-function: ease-out;
  }
}
@keyframes layer-fadeInUpBig {
  0% {
    opacity: 0;
    transform: translateY(2000px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes layer-zoomInLeft {
  0% {
    opacity: 0;
    transform: scale(0.1) translateX(-2000px);
    animation-timing-function: ease-in-out;
  }
  60% {
    opacity: 1;
    transform: scale(0.475) translateX(48px);
    animation-timing-function: ease-out;
  }
}
@keyframes layer-rollIn {
  0% {
    opacity: 0;
    transform: translateX(-100%) rotate(-120deg);
  }
  100% {
    opacity: 1;
    transform: translateX(0px) rotate(0deg);
  }
}
@keyframes layer-fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes layer-shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
@keyframes layer-slide-down {
  from {
    transform: translate3d(0, -100%, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes layer-slide-down-out {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(0, -100%, 0);
  }
}
@keyframes layer-slide-left {
  from {
    transform: translate3d(100%, 0, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes layer-slide-left-out {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(100%, 0, 0);
  }
}
@keyframes layer-slide-up {
  from {
    transform: translate3d(0, 100%, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes layer-slide-up-out {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(0, 100%, 0);
  }
}
@keyframes layer-slide-right {
  from {
    transform: translate3d(-100%, 0, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes layer-slide-right-out {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(-100%, 0, 0);
  }
}
/* 动画 */
.layer-anim {
  animation-fill-mode: both;
  animation-duration: 0.3s;
}

.layer-anim-00 {
  animation-name: layer-bounceIn;
}

.layer-anim-01 {
  animation-name: layer-zoomInDown;
}

.layer-anim-02 {
  animation-name: layer-fadeInUpBig;
}

.layer-anim-03 {
  animation-name: layer-zoomInLeft;
}

.layer-anim-04 {
  animation-name: layer-rollIn;
}

.layer-anim-05 {
  animation-name: layer-fadeIn;
}

.layer-anim-06 {
  animation-name: layer-shake;
}

/* 从上往下 */
.layer-anim-slide-down {
  animation-name: layer-slide-down;
}

.layer-anim-slide-down-out {
  animation-name: layer-slide-down-out;
}

/* 从右往左 */
.layer-anim-slide-left {
  animation-name: layer-slide-left;
}

.layer-anim-slide-left-out {
  animation-name: layer-slide-left-out;
}

/* 从下往上 */
.layer-anim-slide-up {
  animation-name: layer-slide-up;
}

.layer-anim-slide-up-out {
  animation-name: layer-slide-up-out;
}

/* 从左往右 */
.layer-anim-slide-right {
  animation-name: layer-slide-right;
}

.layer-anim-slide-right-out {
  animation-name: layer-slide-right-out;
}

.layer-anim-close {
  animation-name: layer-bounceOut;
  animation-fill-mode: both;
  animation-duration: 0.2s;
}

.layui-anim {
  animation-duration: 0.3s;
  animation-fill-mode: both;
}

.layui-anim.layui-icon {
  display: inline-block;
}

.layui-anim-loop {
  animation-iteration-count: infinite;
}

.layui-anim-rotate {
  animation-name: layui-rotate;
  animation-duration: 1s;
  animation-timing-function: linear;
}

.layui-layer {
  position: fixed;
  pointer-events: auto;
  top: 150px;
  left: 0;
  margin: 0;
  padding: 0;
  background-color: #fff;
  background-clip: content-box;
  border-radius: 2px;
  box-shadow: 1px 1px 50px rgba(0, 0, 0, 0.3);
}
.layui-layer * {
  outline: 0;
}
.layui-layer .layui-layer-close {
  position: absolute;
}
.layui-layer .layui-layer-title {
  padding: 0 81px 0 16px;
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #f0f0f0;
  font-size: 14px;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  border-radius: 2px 2px 0 0;
}
.layui-layer .layui-layer-content {
  position: relative;
}
.layui-layer .layui-layer-setwin {
  position: absolute;
  right: 15px;
  top: 16px;
  font-size: 0;
  line-height: initial;
}
.layui-layer .layui-layer-setwin span {
  position: relative;
  width: 16px;
  height: 16px;
  line-height: 18px;
  margin-left: 10px;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  color: #000;
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
}
.layui-layer .layui-layer-setwin .layui-layer-min::before {
  content: "";
  position: absolute;
  width: 12px;
  border-bottom: 1px solid #2e2d3c;
  left: 50%;
  top: 50%;
  margin: -0.5px 0 0 -6px;
  cursor: pointer;
}
.layui-layer .layui-layer-setwin .layui-layer-min:hover::before {
  background-color: #2d93ca;
}
.layui-layer .layui-layer-setwin .layui-layer-max::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1;
  width: 9px;
  height: 9px;
  margin: -5px 0 0 -5px;
  border: 1px solid #2e2d3c;
}
.layui-layer .layui-layer-setwin .layui-layer-max::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1;
  width: 9px;
  height: 9px;
  margin: -5px 0 0 -5px;
  border: 1px solid #2e2d3c;
}
.layui-layer .layui-layer-setwin .layui-layer-max:hover::before {
  border-color: #2d93ca;
}
.layui-layer .layui-layer-setwin .layui-layer-max:hover::after {
  border-color: #2d93ca;
}
.layui-layer .layui-layer-setwin .layui-layer-maxmin::before {
  width: 7px;
  height: 7px;
  margin: -3px 0 0 -3px;
  background-color: #fff;
}
.layui-layer .layui-layer-setwin .layui-layer-maxmin::after {
  width: 7px;
  height: 7px;
  margin: -3px 0 0 -3px;
  background-color: #fff;
  z-index: 0;
  margin: -5px 0 0 -1px;
}
.layui-layer .layui-layer-setwin .layui-layer-close {
